import React, {memo, useEffect} from 'react';
import Header from "../../../../partials/Header";
import SideBar from "../../../Partials/SideBar";
import {NavLink} from "react-router-dom";
import 'react-perfect-scrollbar/dist/css/styles.css';
import $ from 'jquery';
import M from "materialize-css";
import InitialPackData from "../../../../InitialPackData";

export default memo(() => {


    useEffect(() => {

        $("ul li#load_more").click(function () {
            var load_more = $(this).closest("div.box-package").find(".load_more");
            var price_pol = $(this).closest("div.box-package").find("#price_pol");
            price_pol.addClass("active_pol");
            setTimeout(function () {
                load_more.addClass("active_load_more");
            }, 300)
        })
        $(".load_more .close_icons").click(function () {
            var load_more = $(this).closest("div.box-package").find(".load_more");
            var price_pol = $(this).closest("div.box-package").find("#price_pol");
            load_more.removeClass("active_load_more");
            setTimeout(function () {
                price_pol.removeClass("active_pol");
            }, 300)
        })

        $("ul li#delete_package").click(function () {
            var back_det = $("#back_detail");
            back_det.fadeIn();
            var serial_pack = $(this).closest("div.box-package").attr("serial-pack");
            var appended = '<span class="task-cat red">' + serial_pack + '</span>';
            $("#serial_pp").html('');
            $("#serial_pp").append(appended);
        })

        $("#cancel").click(function () {
            $("#back_detail").fadeOut()
        })
        // load collapse
        var elems = document.querySelectorAll('.collapsible');
        M.Collapsible.init(elems);

    })


    function renderCollapse(detail) {

        // console.log('detail  : ' , detail);
        return Object.keys(detail[0]).map(item => {
            // {console.log("item ln : ",detail[0][`${item}`][0].name)}
            var item_det = detail[0][`${item}`][0];
            return (
                <ul className="collapsible customs-colapse" style={{fontSize: 13}}>
                    <li>
                        <div className="collapsible-header">
                            {item_det.name}
                        </div>
                        <div className="collapsible-body" id="collapse-body_custom">
                                        <span>
                                            <ul>
                                                <li style={{marginBottom: 5}}>
                                            {item_det.type ?
                                                <div>
                                                    <p style={{color: '#00b8d4'}}>Game's type : </p>
                                                    <ul id="type_in_more">
                                                        {item_det.type.map(type => {
                                                            return (
                                                                <li>{type}</li>
                                                            )
                                                        })}
                                                    </ul>
                                                </div>

                                                : ''}

                                                </li>

                                                <li style={{marginBottom: 5}}>
                                            {item_det.time ?
                                                `<span>time : </span>` + item_det.time
                                                :
                                                ''
                                            }
                                                </li>

                                                <li>
                                            {item_det.numOf ?
                                                "numOfRepeat : " + item_det.numOf
                                                :
                                                ''
                                            }
                                                </li>

                                            </ul>



                                        </span>
                        </div>
                    </li>

                </ul>
            )
        })

    }


    function renderSwitch() {
        return InitialPackData.package.map((index) => {
            return (
                <div className="col s6 l3 m4" style={{
                    zIndex: 9,
                    position: "relative",
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    padding: "20px"
                }}>
                    <div className="box-package" serial-pack={index.serial}>
                        <div id="pack_back"></div>
                        <div id="price_pol">
                            <span>{index.price}$</span>
                            <span id="package_serial">#{index.serial}</span>
                        </div>
                        <div id="top_polygon">


                            {index.detail.map((itemLi) => {
                                {
                                    console.log("indexessss : ", itemLi.video[0].name)
                                }
                                return (
                                    <ul>
                                        <li>+ {itemLi.video[0] ? itemLi.video[0].name : 'not register'}</li>
                                        <li>+ {itemLi.voice[0].name ? itemLi.voice[0].name : 'not register'}</li>
                                        <li>+ {itemLi.game[0].name ? itemLi.game[0].name : 'not register'}</li>
                                        <li>+ {itemLi.medicine[0].name ? itemLi.medicine[0].name : 'not register'}</li>
                                        <li>+ {itemLi.ffmetting[0].name ? itemLi.ffmetting[0].name : 'not register'}</li>
                                        <li>+ {itemLi.test[0].name ? itemLi.test[0].name : 'not register'}</li>
                                    </ul>
                                )


                            })}

                        </div>

                        <div id="read_more">
                            <ul>
                                <li id="delete_package" key-attr="1"><i
                                    className="material-icons">delete</i>
                                </li>
                                <li id="load_more">
                                    <i className="material-icons">more_horiz</i>
                                </li>
                                <li><i className="material-icons">edit</i></li>
                            </ul>
                        </div>
                        <div className="load_more" serial-key={index.serial}>
                                                    <span className="close_icons">
                                                        <i className="material-icons">close</i>
                                                    </span>
                            <p className="center-align">pack detail</p>

                            {renderCollapse(index.detail)}

                        </div>
                    </div>
                </div>
            )
        })
    }

    return (
        <div>

            <div id="loader-wrapper">
                <div id="loader"></div>
                <div className="loader-section section-left"></div>
                <div className="loader-section section-right"></div>
            </div>

            <Header/>
            <div id="main">
                <div className="wrapper">


                    <SideBar/>

                    <div id="back_shado"></div>
                    <div className="container">
                        <div className="row">
                            <div className="col s12">

                                <div className="col s12">
                                    <div id="breadcrumbs-wrapper">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col s10 m6 l6">
                                                    <h5 className="breadcrumbs-title">Package Manager</h5>
                                                    <ol className="breadcrumbs">
                                                        <li><NavLink to="/admin">
                                                            <a>Dashboard</a>
                                                        </NavLink></li>
                                                        <li><a className="active">Package Manager</a></li>
                                                    </ol>
                                                </div>

                                            </div>
                                        </div>

                                        <NavLink to="/package-add"><a id="adding_float_btn"
                                                                      className="btn btn-floating green darken-2 waves-effect">
                                            <i className="material-icons">add</i>
                                        </a>
                                        </NavLink>
                                    </div>
                                </div>


                                <div className="col s12">
                                    {renderSwitch()}
                                </div>


                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <div id="back_detail">
                <div className="container">
                    <div className="row">
                        <div className="col s12">
                            <div className="col s1 m2 l4"></div>
                            <div className="col s10 m8 l4" style={{marginTop: 30}}>
                                <div className="card grey darken-4">
                                    <div className="card-content white-text">
                                        <span className="card-title">Delete Package</span>
                                        <p>Do you want to delete the <span id="serial_pp"></span> package?</p>
                                    </div>
                                    <div className="card-action">
                                        <a className="btn btn-secondary waves-effect red" id="deleted" style={{
                                            textTransform: 'lowercase',
                                            marginBottom: 15,
                                            float: "left"
                                        }}>delete</a>
                                        <a className="btn btn-secondary waves-effect blue darken-3" id="cancel"
                                           style={{
                                               textTransform: 'lowercase',
                                               marginBottom: 15,
                                               float: 'right'
                                           }}>back</a>

                                    </div>
                                </div>
                            </div>
                            <div className="col s1 m2 l4"></div>
                        </div>
                    </div>
                </div>

            </div>


        </div>
    );
})


