import React, {useEffect} from "react";
import $ from 'jquery'
import {useTranslation} from "react-i18next";
import M from 'materialize-css';
// eslint-disable-next-line react-hooks/rules-of-hooks
export default () => {
    useEffect(() => {
        var elems = document.querySelectorAll('.sidenav');
        M.Sidenav.init(elems);
        $(".sidebar-collapse").click(function (){
            $(".side-nav.fixed.leftside-navigation").css("transform" , "translateX(0px)");
        })
        // $('body').click(function ()
        // {
        //     $(".side-nav.fixed.leftside-navigation").css("transform" , "translateX(-100%)");
        // })
    })
    const {i18n} = useTranslation();
    function handleLang(e) {
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", 'en');
        }
        var mini = $(".mini-box");
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === 'en') {
            $('.mini-box #one').css({display: 'none'});
            $('.mini-box #tow').css({display: 'flex'});
            $('.box_switch .one img').css({display: 'block'});
            $('.box_switch .tow img').css({display: 'none'});
            mini.addClass('actives')
            localStorage.setItem("lan", 'ch');
        } else {
            $('.mini-box #tow').css({display: 'none'});
            $('.mini-box #one').css({display: 'flex'});
            $('.box_switch .one img').css({display: 'none'});
            $('.box_switch .tow img').css({display: 'block'});
            mini.removeClass('actives');
            localStorage.setItem("lan", 'en');
        }
        let lang = localStorage.getItem('lan');
        i18n.changeLanguage(lang);
    }


    return (
        <header id="header" className="page-topbar">
            <div className="navbar-fixed">
                <nav className="navbar-color gradient-45deg-light-blue-cyan-header">
                    <div className="nav-wrapper">
                        <ul className="left">
                            <li>
                                <h1 className="logo-wrapper">
                                    <a className="brand-logo darken-1">
                                        <span>Bi Con</span>
                                    </a>
                                </h1>
                            </li>
                        </ul>
                        <ul className="right hide-on-med-and-down">
                            <li>
                                <div className="box_switch">
                                    <input onClick={handleLang} name="cahngelan"
                                           checked={localStorage.getItem("lan") === 'en' ? false : true} type="checkbox"
                                           id="switcher"/>
                                    <div
                                        className={`mini-box ${localStorage.getItem('lan') === 'ch' ? 'actives' : ''}`}>
                                        <span id="one"
                                              style={{display: `${localStorage.getItem("lan") === 'en' ? 'flex' : 'none'}`}}>En</span>
                                        <span id="tow"
                                              style={{display: `${localStorage.getItem("lan") === 'ch' ? 'flex' : 'none'}`}}>Ch</span>
                                    </div>
                                    <span className="one">
                                        <img
                                            style={{display: `${localStorage.getItem("lan") === 'en' ? 'none' : 'block'}`}}
                                            src="/img/icons/china.png"/>
                                    </span>
                                    <span className="tow">
                                        <img
                                            style={{display: `${localStorage.getItem("lan") === 'ch' ? 'none' : 'block'}`}}
                                            src="/img/icons/united-kingdom.png"/>
                                    </span>
                                </div>
                            </li>

                            <li>
                                <a href="javascript:void(0);"
                                   className="waves-effect waves-block waves-light toggle-fullscreen">
                                    <i className="material-icons">settings_overscan</i>
                                </a>
                            </li>
                        </ul>
                    </div>
                </nav>
            </div>
        </header>
    )
}
